.MovieCard {
  position: relative;
  display: block;
  margin: 20px 0;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.4s;
  box-shadow: 0px 0px 120px -25px rgba(0, 0, 0, 0.5);
  padding: 25px;
  background: linear-gradient(to right, #e5e6e6 50%, transparent 100%);
}

.MovieCard__info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.MovieCard__header {
  display: flex;
  height: 40%;
}

.MovieCard__name {
  width: 80%;
  color: black;
  font-size: 50px;
  font-weight: 700;
}

.MovieCard__year {
  color: #555;
  font-weight: 700;
}

.MovieCard__rating {
  text-align: center;
  width: 50px;
  color: rgb(255, 255, 255);
  padding: 5px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  background-color: #596fe9;
}

.MovieCard__genres {
  width: 80%;
  color: #959595;
}

.MovieCard__img {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 360px;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}

.MovieCard__desc {
  height: 50%;
}

.MovieCard__text {
  color: #545454;
  font-size: 20px;
  line-height: 1.5;
  margin: 20px 0;
  width: 65%;
}

.MovieCard__additionalInfo {
  display: flex;
}

.MovieCard__additionalInfo ul {
  list-style: none;
  padding: 0;
}

.MovieCard__additionalInfo_item {
  text-decoration: none;
  display: inline-block;
  color: rgba(0, 0, 0, 0.3);
  transition: color 0.3s;
  transition-delay: 0.15s;
  margin: 0 10px;
}
.MovieCard__additionalInfo_item:hover,
.MovieCard__additionalInfo_item:focus {
  transition: color 0.3s;
  color: rgba(0, 0, 0, 0.7);
}

.MovieCard__additionalInfo_item_active {
  color: rgba(0, 0, 0, 0.7);
}

.blur_back {
  position: absolute;
  top: 0;
  z-index: -1;
  height: 100%;
  right: 0;
  background-size: cover;
  border-radius: 11px;
  width: 100%;
  background-repeat: no-repeat;
}
